.social {
    margin-bottom: 40px;
    width: 100%;
    min-width: 100%;
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: white;

    .button {
        flex: 1;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        outline: none !important;
        border: none;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
        background-color: white;
        border: 1px solid rgba(gray, 0.6);
        margin: 0px 10px 20px 10px;

        &:first-child {
            margin-left: 0px;
        }

        &:last-child {
            margin-right: 0px;
        }

        @media screen and (max-width: 992px) {
            margin: 0px 5px 0px 5px;
        }


        @media screen and (max-width: 576px) {}

        &:hover {
            cursor: pointer;
        }

        .icon {
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white !important;
        }

        .title {
            font-size: 20px;
            font-family: sans-serif;
            font-weight: 900;
            display: -webkit-box;
            max-width: 400px;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
        }

    }
}