.categoryArticle {
    width: 100vw;
    height: auto;
    padding-top: 60px;
    margin-bottom: 0px;
    background-color: #ffffff;

    article.category-detail-article {
        @media screen and (max-width:992px) {
            padding: 0px 10px;
        }

        figure.category-image {
            margin-top: 10px;
            margin-bottom: 20px;
            width: 650px;
            max-width: 100%;
            object-fit: contain;
            float: left;
            margin-right: 40px;
            padding-right: 10px;

            @media screen and (max-width:992px) {
                width: 100%;
                margin-top: -30px;
            }

            img {
                width: 100%;
                object-fit: contain;
                border: 1px solid rgba(0, 0, 0, 0.3)
            }
        }

        .category-details-texts {

            .category-title {
                padding-top: 15px;
                font-size: 37px !important;
                font-weight: bold;
                line-height: 48px !important;
                letter-spacing: -1.1px;
                font: 700 42px/55px sans-serif;
                color: #000000;
                margin-bottom: 20px;
            }

            .category-description {
                font-size: 25px;
                font-weight: normal;
                letter-spacing: -0.25px;
                font-family: sans-serif;
                color: #555555;
                margin-bottom: 20px;
            }

        }
    }

    .top-container {
        margin-top: 40px;
        height: 100%;
        display: flex !important;
        justify-content: center;
        align-items: center;

        .blog-container {
            width: 100%;
            position: relative;

            &::-webkit-scrollbar {
                display: none;
            }

            .blog-card {
                min-height: 450px;
                width: 18rem;
                border-radius: 6px;
                padding: 0px !important;
                display: inline-block !important;
                margin: 20px 0px 0px 0px;
                width: 98.5%;
                justify-content: center;
                display: flex;
                white-space: normal;
                box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                -webkit-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                -moz-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);

                &:hover {
                    transition: all 0.15s ease-in-out;
                    transform: scale(1.085);
                    border-color: #ACCEF7;

                }

                img {
                    cursor: pointer;
                }

                .blog-body {
                    min-height: 200px;
                    max-height: 430px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    height: 285px;

                    .blog-information {
                        font-family: sans-serif;
                        color: #8a8a8a;
                        letter-spacing: -0.5px;
                        font-size: 19px;
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        color: black;

                        * {
                            font-weight: bold;
                        }

                        .blog-topic {}

                        .blog-pub-date {}
                    }

                    .blog-title {
                        font-family: inherit;
                        font-weight: bold;
                        letter-spacing: -1.1px;
                        display: -webkit-box;
                        height: 62px;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        font-family: sans-serif;
                        font-size: 22px;
                        line-height: 31px;
                    }

                    .blog-content {
                        font-family: inherit;
                        font-size: 18px;
                        color: #555555;
                        font-weight: normal;
                        letter-spacing: -0.25px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        -webkit-line-clamp: 5;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        max-height: 300px;
                        margin-top: 15px;
                        min-height: 130px;
                        margin-bottom: 15px;
                    }

                    hr {
                        margin: 0px -10px 0px -10px !important;
                    }

                    .readBtn {
                        width: 100%;
                        height: 50px;
                        position: relative;
                        margin: 5px 0px 5px 0px;
                        top: 5px;
                        font-size: 18px;
                        letter-spacing: 1px;
                        text-decoration: none;
                        font-weight: bold;
                        background-color: #000000;
                        color: white;
                        outline: none;
                        border: none !important;
                        border-radius: 3px;
                    }
                }
            }
        }
    }
}